export default {
	data() {
		return {
			disabled: false,
			repayChecked: true,
			code: 0
		};
	},
	components: {},
	computed: {},
	methods: {
		switchChange() {
			this.$store.dispatch('setCashierStatus',{
				isopen: !this.repayChecked?1:0
			}).then(res=>{
				this.repayChecked = !this.repayChecked
			}).catch(err=>{
			})
		},
		logout(){
			uni.showModal({
				title: '提示',
				content: '确定退出登录？',
				success: (res)=>{
					if(res.confirm){
						uni.removeStorageSync('token');
						uni.reLaunch({
							url: '/pages/login/login'
						})
					}
				}
			})
		}
	},
	onShow(){
		this.$store.dispatch('getCashierStatus',{
			type: 'cashier'
		}).then(res=>{
			this.repayChecked = res.isopen==0?false:true
		}).catch(err=>{})
	}
}
